.summary {
    width: 460px;
    /* border: 1px solid red; */
    margin-left: 32px;
}

/* Coupon */
.coupon {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-top: 1px solid #ccc;
    border-bottom: 1px solid #ccc;
    padding: 18px 0;
}

.coupon input {
    border: 1px solid #ccc;
    padding-left: 12px;
    color: #333;
    font-size: 14px;
    height: 50px;
    width: 65%;
}

.coupon button { width: 120px;}

/* Order Receipt */
.order-receipt {
    border-bottom: 1px solid #ccc;
}

.text-receipt {
    margin: 20px 0;
}

.text-receipt span:first-child {
    color: #6e6e6e;
}

.text-receipt span:last-child {
    font-weight: bold;
}

.total {
    font-size: 1.5rem;
}

